import { _decorator, Component, Node, AudioClip, AudioSource } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Audio')
export class Audio extends Component {
    @property(AudioClip)
    public clips: AudioClip [] = [];
    @property(AudioSource)
    public audioSource: AudioSource = null;
    @property(AudioSource)
    public audioSource1: AudioSource = null;
    @property(Node)
    public turnOffAudioBtn: Node = null;
    @property(Node)
    public turnOnAudioBtn: Node = null;
    @property(Node)
    public turnOffMusicBtn: Node = null;
    @property(Node)
    public turnOnMusicBtn: Node = null;

    public onOffAudio: boolean = true;
    public onOffMusic: boolean = true;
    public clip;
}


